import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import '../global.css';

const educationStyles = makeStyles({
  root: { backgroundColor: `#303030`, borderTop: `2px solid gold`, marginTop: `10px` },
  cardTitle: {
    color: `#fff`,
    fontSize: `1.38316rem`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
  school: {
    fontSize: `17px`,
    lineHeight: `1.3`,
    color: `#fff`,
    fontWeight: `600`,
  },
  date: { color: `#fff`, opacity: `.9` },
  details: {
    color: `#fff`,
    fontSize: `16px`,
    lineHeight: `1.5`,
    margin: `10px 0 0 0`,
  },
});

const Education = () => {
  const classes = educationStyles();
  return (
    <Card className={classes.root}>
      <CardContent>
        <div className={classes.cardTitle}>
          <span role={'img'} aria-label={'Cap emoji'}>
            🎓
          </span>{' '}
          Education
        </div>
        <div className={classes.school}>Louisiana State University</div>
        <div className={classes.date}>May 2020</div>
        <div className={classes.details}>
          - Computer Science, Bachelor of Science <br />
          - Information Technology Management (ITM) Minor
        </div>
      </CardContent>
    </Card>
  );
};

export default Education;
