import React from 'react';
import { graphql } from 'gatsby';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { makeStyles } from '@material-ui/core/styles';
import Experience from '../components/experience';
import Layout from '../components/layout';
import SEO from '../components/seo';
import Education from '../components/education';
import '../global.css';

const indexStyles = makeStyles({
  cardTitle: {
    color: `#fff`,
    fontSize: `1.38316rem`,
    fontWeight: 800,
    marginBottom: `10px`,
  },
});

const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const posts = data.allMarkdownRemark.edges;
  const localStyles = indexStyles();

  return (
    <Layout
      location={location}
      title={siteTitle}
      style={{ display: `flex`, justifyContent: `center` }}
    >
      <SEO
        title="Andrew Stolzle"
        lang="en"
        description="All posts by Andrew Stolzle"
      />
      <Experience />
      <Education />
      <Card
        style={{
          backgroundColor: `#303030`,
          display: `flex`,
          flexWrap: `wrap`,
          marginTop: `10px`,
          borderTop: `2px solid #03DAC6`,
        }}
      >
        <CardContent>
          <div className={localStyles.cardTitle}>
            <span role={'img'} aria-label={'Folder emoji'}>
              📂
            </span>{' '}
            Recent Projects
          </div>
          {posts.map(({ node }) => {
            const title = node.frontmatter.title || node.fields.slug;
            return (
              <Card key={node.fields.slug} className={`project-card`}>
                <div className={`proj-card-title`}>{title}</div>
                <CardContent>
                  <p
                    style={{ fontSize: `16px` }}
                    dangerouslySetInnerHTML={{
                      __html: node.frontmatter.description || node.excerpt,
                    }}
                  />
                </CardContent>
              </Card>
            );
          })}
        </CardContent>
      </Card>
    </Layout>
  );
};

export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`;
